.container {
  margin: 0 auto;
  height: 130px;
  width: 300px;
  margin-top: 60px;
  background-color: rgb(229, 238, 235);
  border: 1px solid rgb(247, 238, 238);
  border-radius: 15px;
  background-color: rgb(229, 238, 235);
  -webkit-box-shadow: 0px 7px 14px -7px rgba(34, 60, 80, 0.6);
  -moz-box-shadow: 0px 7px 14px -7px rgba(34, 60, 80, 0.6);
  box-shadow: 0px 7px 14px -7px rgba(34, 60, 80, 0.6);
}
