.modal {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  display: flex;
  background-color: rgba(0, 0, 0, 0.2);
  backdrop-filter: blur(10px);
  display: none;

  .modal__body {
    width: 50%;
    height: 50%;
    margin: auto;
    background: white;
    border-radius: 10px;
  }
}

.modal_opened {
  display: flex;
}

.modal__wrapper-close {
  height: 0;
  display: none;
}
